.searchcard{
    display: flex;
    align-items: center;
    gap: 30px;
    color: white;
    width: 100%;
    padding: 0 30px;
    &:hover{
        transition: 1s;
       color: #DA00FE;
       transform: scale(1.05);
    }
    img{
        width: 100px;
        height: 100px;
    }
}